<template>
	<view class="content">
		<u-row>
			<u-col span="12" class="tabsList">
				<u-tabs active-color="#fff" inactive-color="#B1D1FF" :list="list" :current="current"
					@change="change"></u-tabs>
			</u-col>
		</u-row>
		<view style="margin: -120rpx 30rpx 0 30rpx;">
			<scroll-view scroll-y style="height: 1100rpx;" v-show="current==0">
				<view class="list01" v-for="(item,index) in list01" :key="index">
					<u-row>
						<u-col span="12">
							<view class="blueline"></view>
							<span>{{item.scoreType}}</span>
							<p>{{item.content}}</p>
							<p>{{item.scoreDate}}</p>
							<view class="addtxt">+{{item.increase}}</view>
						</u-col>
					</u-row>
				</view>
			</scroll-view>
			<scroll-view scroll-y style="height: 1100rpx;" v-show="current==1">
				<view class="list01" v-for="(item,index) in list01" :key="index">
					<u-row>
						<u-col span="12">
							<view class="blueline"></view>
							<span>{{item.scoreType}}</span>
							<p>{{item.content}}</p>
							<p>{{item.scoreDate}}</p>
							<view class="addtxt">+{{item.increase}}</view>
						</u-col>
					</u-row>
				</view>
			</scroll-view>
		</view>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		scoreAInfo,
		scoreBInfo
	} from '../../api/index.js'
	export default {
		data() {
			return {
				params: {
					pageNum: 1,
					pageSize: 10
				},
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: 'A积分'
				}, {
					name: 'B积分'
				}],
				list01: [],
			}
		},
		onLoad() {
			this.todo();
		},
		methods: {
			todo() {
				scoreAInfo(this.params).then(res => {
					if (res.code == 200) {
						this.list01 = res.rows;
					}
				})
			},
			finished() {
				scoreBInfo(this.params).then(res => {
					if (res.code == 200) {
						this.list01 = res.rows;
					}
				})
			},
			change(index) {
				this.current = index;
				if (index == 1) this.finished();
				else this.todo();
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background-color: #F5F6FA;
	}

	.addtxt {
		font-weight: bold;
		position: absolute;
		top: 20rpx;
		right: 30rpx;
	}

	.btn {
		background-color: #296DCB;
		color: #fff;
		font-size: 24rpx;
		margin-top: 40rpx;
	}

	.tabsList {
		background-color: #1F61BE;
		min-height: 203rpx;
		border-radius: 0 0 10rpx 10rpx;
	}

	.tabsList /deep/ .u-tabs {
		background: transparent !important;
	}

	.topBox {
		position: relative;
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
	}

	.topBox .ss {
		position: absolute;
		right: -10rpx;
		top: 8rpx;
		width: 30rpx;
		height: 30rpx;
	}

	.grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.list01 {
		width: 100%;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 20rpx 20rpx 20rpx 30rpx;
		position: relative;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 26rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list01 span,
	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.list01 p,
	.list02 p {
		margin-top: 10rpx;
		font-size: 24rpx;
		color: #999999;
		width: 96%;
		overflow: hidden;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}

	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.listimg {
		width: 208rpx;
		height: 126rpx;
	}

	.list02 {
		width: 100%;
		height: 231rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 17rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>